import React, { Component } from "react";

export class Projects extends Component {
  render() {
    return (
      <div>
        <header className="App-header">
          <div className="container-fluid">
            <div className="row">
              <div className="col-8 offset-2 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-6 offset-lg-3 col-xl-4 offset-xl-4">
                <h1 style={projectHeader}>My Projects</h1>
                <p style={ temp }>(this page is still a work in progress)</p>
                <div className="project" style={projectLayout}>
                  <div className="row">
                    <p style={projectTitle}>Todolist</p>
                  </div>
                  <div className="row">
                    <span style={projectDesc}>
                      Cause you can never have too many todolists.
                    </span>
                  </div>
                </div>
                <br></br>
                <div className="project" style={projectLayout}>
                  <div className="row">
                    <p style={projectTitle}>Accounting tool</p>
                  </div>
                  <div className="row">
                    <span style={projectDesc}>
                      A specific end-of-day report generation tool.
                    </span>
                  </div>
                </div>
                <br></br>
                <div className="project" style={projectLayout}>
                  <div className="row">
                    <p style={projectTitle}>BorgerBot</p>
                  </div>
                  <div className="row">
                    <span style={projectDesc}>
                      A discord bot that makes shitty burgers
                    </span>
                  </div>
                </div>
                <br></br>
              </div>
            </div>
          </div>
        </header>
      </div>
    );
  }
}

const projectHeader = {
  padding: "1.2em 0em",
  fontSize: "calc( 30px + (40 - 30) * (100vw - 400px)/(800-400) )",
  animation: "fadein1 4s"
};

const projectLayout = {
  background: "inherit",
  padding: "1.5em 1.5em 1.5em 1.5em",
  borderRadius: "20px",
  transition: "all 0.5s",
  borderStyle: "solid",
  animation: "fadein1 4s"
};

const projectTitle = {
  fontSize: "calc( 20px + (26 - 20) * (100vw - 400px)/(800-400) )",
  width: "100%",
};

const projectDesc = {
  fontSize: "calc( 15px + (20 - 15) * (100vw - 400px)/(800-400) )",
  width: "100%",
};

const temp = {
  animation: "fadein1 4s"
}

export default Projects;
