import React, { Component } from "react";
import { Link } from 'react-router-dom';

export class main extends Component {

  render() {
    return (
      <div>
        <header className="App-header">
          <div className="container-fluid">
            <div className="row">
              <div id="fader" className="col-10 offset-1 col-sm-10 offset-1 col-md-6 offset-md-3">
                <h1 style={mainText}>Hi.</h1>
                <p style={introText}>
                  I'm <a href="https://www.linkedin.com/in/danj/" target="_blank" rel="noopener noreferrer">Dan Jiang</a>, a Software Engineering student.
                </p>
                <p style={welcomeText}>Welcome to my site.</p>
                <Link className="button" style={ buttonStyle } to="/feather/projects"> <span>My Projects</span></Link>
              </div>
            </div>
          </div>
        </header>
      </div>
    );
  }
}

const mainText = {
  fontSize: "calc( 100px + (150 - 100) * (100vw - 400px)/(800-400) )",
  textAlign: "left",
  marginLeft: "-0.07em",
  animation: "fadein1 6s"
};

const introText = {
  textAlign: "left",
  fontSize: "calc( 15px + (20 - 15) * (100vw - 400px)/(800-400) )",
  animation: "fadein2 6s"
};

const welcomeText = {
  textAlign: "left",
  fontSize: "calc( 16px + (24 - 16) * (100vw - 400px)/(800-400) )",
  fontWeight: "bold",
  animation: "fadein3 6s"
};

const buttonStyle = {
  fontSize: "calc( 10px + (15 - 10) * (100vw - 400px)/(800-400) )",
  animation: "fadein4 6s"
}


export default main;
